import React,{useState,useEffect} from 'react';
import {HashRouter as Router,Redirect,Route,NavLink,Switch} from 'react-router-dom'
import './App.css';
import Header from './Header'
import routes from './Router.config';
import{Button,SearchBar,TabBar} from'antd-mobile';
import Mytab from'../taobao/Mytab';
import Home from '../taobao/Home';
import Cart from '../taobao/Cart';
import SearchBarExample from '../taobao/searchBar';
import DetailExample from '../taobao/details';
//淘宝作业
const App=()=>{
	return <Router>
		<Switch>
		{/* <Route  path='/Home' component={Home} /> */}
		<Route exact path='/' component={Home}/>
		<Route path='/cart' component={Cart}/>
            <Route path='/home/searchBar' component={SearchBarExample}/>
            <Route path='/home/details' component={DetailExample}/>
         {/* <Route render={()=><Redirect to='/'/>}/> */}
		</Switch>
		<Mytab />
	</Router>
}



